import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { GlobalContext } from "../context/GlobalState";
import { BsPencil } from "react-icons/bs";
import { MdDeleteForever } from "react-icons/md";
import Axios from "axios";
import styles from "./BookList.module.css";
import Button from "../UI/Button/Button";

const UserList = () => {
    const { books, removeUser } = useContext(GlobalContext);

    const removeHandler = (id) => {
        removeUser(id);
        Axios.delete(`https://canary-cqbc.onrender.com/delete/${id}`);
    };

    return (
        <table className={styles["content-table"]}>
            <thead>
                <tr>
                    <th className={styles.title}>Domain name</th>
                    <th className={styles.author}>Type</th>
                    <th className={styles.category}>Value</th>
                    <th className={styles.price}>Price</th>
                    <th className={styles.price}>Actions</th>
                </tr>
            </thead>
            {books.length > 0 && (
                <tbody>
                    {books.map((book) => {
                        return (
                            <tr key={book._id}>
                                <td>{book.bookName}</td>
                                <td>{book.bookAuthor}</td>
                                <td>{book.bookCategory}</td>
                                <td>{book.bookPrice}</td>
                                <td>
                                    <div className="actions">
                                        <Link
                                            to={`/edit/${book._id}`}
                                            id={styles.link}
                                            className={styles.link}
                                        >
                                            <BsPencil />
                                            Edit
                                        </Link>
                                        <Button
                                            onClick={() =>
                                                removeHandler(book._id)
                                            }
                                            className={styles.button}
                                        >
                                            <MdDeleteForever />
                                            Delete
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        );
                    })}
                </tbody>
            )}
        </table>
    );
};

export default UserList;
